.nav {
  display: flex;
  position: relative;
  width: 100%;
  max-width: 600px;

  a {
    text-decoration: none;
    color: white;
    position: relative;
    z-index: 9;
    padding: 15px 0px;
    flex: 1;
    text-align: center;

    &:hover {
      background: #333;
      color: #fff;
    }
  }

  &::before {
    content: "";
    display: block;
    position: absolute;
    left: -50px;
    top: 0;
    width: 1px;
    height: 100vh;
    background-color: hsla(0, 0%, 40%, .3);
  }
}
