.main {
  background-color: #202020;
  color: white;

}

.container {
  display: flex;
  text-align: center;
  font-size: 30px;
  flex-wrap: wrap;
  max-width: 1470px;
  margin-left: auto;
  justify-content: center;
  margin-right: auto;
}

.textBox {
  display: flex;
  align-items: center;
  padding: 50px;

}


.wrapper {
  margin: 0 auto;
  padding: 50px 50px;
  border: 1px solid hsla(0, 0%, 40%, .3);
  background-color: #111111;
}


.photo {

  img {
    max-width: 35%;
  }
}
